<template>

      <pageLayout v-if="authorized">

        <solarpanel style="width:10%; height: 10%;"></solarpanel>
        <solarpanel style="width:15%; height: 15%;"></solarpanel>
        <solarpanel style="width:20%; height: 20%;"></solarpanel>
        <solarpanel style="width:30%; height: 30%;"></solarpanel>
    </pageLayout>

</template>

<script>

  //import Alert from '../Modals/alert.vue';
  import axios from 'axios';
  //import navbar from '../Modals/navbar.vue';
  import pageLayout from '../Modals/page.vue'
  import solarpanel from "../Modals/solarPanel";


  export default{

    name: 'index',
    // mounted(){
    //   this.authorize();
    // },
    components: {
      solarpanel,
      pageLayout
    },
    methods: {

      async authorize() {

        if(!this.$cookies.isKey('devName') && !this.$cookies.isKey('appKey')) {

          const {value: formValues} = await this.$swal({
            title: 'Authorization',
            confirmButtonText: 'Submit',
            confirmButtonClass: 'btn btn-success',
            html:
            '<input id="swal-input1" class="swal2-input" placeholder="Device name">' +
            '<input id="swal-input2" class="swal2-input" placeholder="App Access Key">' +
            '<input id="swal-checkbox1" class="swal2-checkbox" type="checkbox" value="1"><span>' +
            'Remember me</span>',
            focusConfirm: false,
            preConfirm: () => {
              return [
                document.getElementById('swal-input1').value,
                document.getElementById('swal-input2').value,
                document.getElementById('swal-checkbox1').value
              ]
            }
          });

          if (formValues) {
            await this.checkCredentials(formValues[0], formValues[1]);

            console.log(this.authorized);

            if (this.authorized=='1') {
              await this.$swal(
                'Authorized!',
                'Your app has been successfully authorized!',
                'success'
              );
              console.log("Here");
              if (formValues[2] == 1) {
                this.$cookies.set('devName', formValues[0]);
                this.$cookies.set('appKey', formValues[1]);
                console.log("Cookies set");
              }
            }
            else {
              await this.$swal(
                'Error',
                'Invalid credentials',
                'error'
              );
              console.log("Actually here");
              this.authorize();
            }
          }


        }
        else
        {
          await this.checkCredentials(this.$cookies.get('devName'),this.$cookies.get('appKey'));

          if(!this.authorized)
          {
            this.$cookies.remove('devName');
            this.$cookies.remove('appKey');
            this.authorize();
          }
          else {
            await this.$swal(
              'Authorized by cookies!',
              'Your app has been successfully authorized!',
              'success'
            );
          }
        }

      },
      async checkCredentials(_name,_key){
        await axios.post(/auth/,
          {
            name: _name,
            key: _key
          })
          .then(response => {

            this.authorized=response.data;
            console.log("Authorization response:" + this.authorized)
          })
          .catch(e => {
            console.log("ERROR:", e);
          })
      },
      getMeasurements(){

        axios.get(window.ApiUrl + /measurements/)
          .then(response=>{
            this.posts=response.data;
            console.log(this.posts)
          })
          .catch(e=>{console.log("ERROR:",e);
          })
      }
    },

    mounted(){
      this.authorize();
      this.getMeasurements();
    },
    data () {
      return {
        authorized:false,
        posts: [],
        errors: []
      };
    }
  };
</script>

<style scoped>



  div{

    /*border-color: white;*/
    /*border-style: solid;*/

  }

  #divPage{

    position: relative;
    width:100%;
    height: auto;

    /*border-color:green;*/

  }
  #blur {

    position: absolute;
    background: url("../../utils/3.jpeg") no-repeat;
    width:100%;
    height:100%;
    background-size:cover;
    -webkit-filter: blur(4px);
    -moz-filter: blur(4px);
    -ms-filter: blur(4px);
    -o-filter: blur(4px);
    filter: blur(3px);
    /*border-color:red;*/
    /*border-style: solid;*/
    z-index: -1;
  }
  .opac{
    position: absolute;
    background-color: #86a6b4;
    opacity:0.3;
    width:100%;
    height: 100%;
    z-index:0;
  }

  #divPageDiv{
    position: relative;
    margin:auto;
    width:80%;
    height: auto;
    /*border-color:blue;*/
  }

  #divPageFlexbox{

    position: relative;
    width:80%;
    min-height: 10%;
    height: auto;
    top:5%;
    left:10%;
    right: 10%;
    bottom:5%;
    /*border-style: solid;*/
    /*border-color: #a50003;*/

  }



</style>



